<template>
  <div class="news">
    <div class="header">
      <span @click="$root.$router.go(-1)">&lt;</span>
      <span>{{news}}</span>
    </div>
    
    <div class="div">
      <ul v-for="(item,index) in list" :key="index+1">
        <li>{{item}}<img :src='$store.state.logindata.auth_inco'></li>
      </ul>
    </div>
 
    <div class="send">
      <input type="text"  v-model="values" />
      <button @click="send">发送</button>
    </div>
  </div>
</template>
<script>
 import {mapGetters} from 'vuex'
export default {

  data() {
    return {
      
      values:'',
      now:''
    }
  },
 
  methods:{
    send(){
      this.$store.dispatch('send',this.values)
    }
  },
mounted(){
    this.$store.dispatch('news',this.$route.params)
  },

  computed:{
    ...mapGetters(['list','news'])
  }
 
};

  

</script>>

